
html,body{height:100%;width:100%;padding:0;margin:0;font-size:14px;font-family:Microsoft Yahei,"PingHei","Helvetica Neue","Helvetica","STHeitiSC-Light","Arial",sans-serif;color:#333
}
#app{height:100%;width:100%;display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;-ms-flex-pack:justify;justify-content:space-between;-ms-flex-align:center;align-items:center
}
#app .head{width:100%
}
#app .head h1{font-size:0;border-bottom:1px solid #aaa;text-align:center;width:100%
}
#app .head h1 img{max-width:20%;max-height:60px;padding:5px 0
}
#app .head .output{background:#fff;border-bottom:1px solid #aaa;width:100%;height:40rem;text-align:center;margin-bottom:10px;display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center
}
#app .head .output .uploader{width:40%;height:20%;background:none;border:1px solid #aaa;outline:none
}
#app .head .output .uploader:active{background:#333;color:#fff
}
#app .head .output img{max-width:100%;max-height:100%;object-fit:contain;cursor:pointer
}
@media (max-width:738px){
#app .head .output{height:20rem
}
}
#app .generate-btn{width:95%;height:4rem;margin-bottom:10px;background:none;border:1px solid #aaa;font-size:18px;outline:none
}
#app .generate-btn:active{background:#333;color:#fff
}
#app .generate-btn .spinner{display:inline-block;width:1.2rem;height:1.2rem;line-height:1.2rem;font-size:1.2rem;animation:spin 1.2s infinite
}
@keyframes spin{
from{transform:rotate(0)
}
to{transform:rotate(360deg)
}
}


